









































































import {
  computed, defineComponent, PropType, Ref,
} from '@vue/composition-api';
import {
  ComboBoxEditorControl, ColorBoxEditorControl, CheckboxToggleEditorControl,
} from '@/app/editor/client/view/component/control';
import {
  ComboBoxChoice,
} from '@/app/editor/client/view/component/control/ComboBoxEditorControl.vue';
import {
  ActionLinkAppearance, ActionLinkDefaults,
  ActionLinkSettings, ActionLinkSize, ButtonStyle,
} from '@/app/shared/client/type/settings/action-link-settings';
import { SystemFont, GoogleFont } from '@/app/shared/client/type/font';
import {
  fontFamilyValue,
  fontKeyLabelList,
  fontWithFallback,
  getFontIfExists,
} from '@/app/shared/client/logic/font';
import { Color } from '@/app/shared/client/type/color';
import { GlobalDesign } from '@/app/shared/client/type/tile/global';
import {
  calculateActualColorDefault,
  calculateActualFontDefault,
} from '@/app/editor/client/logic/defaults';
import useI18N from '@/app/shared/client/logic/i18n';

function useAppearanceChangeHandler(
  settings: Readonly<Ref<Readonly<ActionLinkSettings>>>,
  emit: (event: string, arg: ActionLinkSettings) => void,
): (value?: ActionLinkAppearance) => void {
  function onAppearanceChange(value?: ActionLinkAppearance): void {
    const newSettings = {
      ...settings.value,
      appearance: value,
    };
    emit('editor-action', newSettings);
  }

  return onAppearanceChange;
}

function useFontChangeHandler(
  settings: Readonly<Ref<Readonly<ActionLinkSettings>>>,
  emit: (event: string, arg: ActionLinkSettings) => void,
): (value?: SystemFont | GoogleFont) => void {
  function onFontChange(value?: SystemFont | GoogleFont): void {
    const newSettings = {
      ...settings.value,
      font: value,
    };
    emit('editor-action', newSettings);
  }

  return onFontChange;
}

function useSizeChangeHandler(
  settings: Readonly<Ref<Readonly<ActionLinkSettings>>>,
  emit: (event: string, arg: ActionLinkSettings) => void,
