    emit('editor-action', newSettings);
  }

  return onVisibleChange;
}

function calculateSizeChoices(
  defaultSize: ActionLinkSize | undefined,
  i18n: (label: string) => string,
): Array<{ key: string | undefined, label: string }> {
  const choices = [
    { key: ActionLinkSize.SMALL, label: i18n('CommonLabels.ActionLink.Size.S') },
    { key: ActionLinkSize.MEDIUM, label: i18n('CommonLabels.ActionLink.Size.M') },
    { key: ActionLinkSize.LARGE, label: i18n('CommonLabels.ActionLink.Size.L') },
  ];

  const defaultSizeLabel = choices
    .find((choice) => defaultSize === choice.key)
    ?.label ?? '';

  const defaultSizeText = i18n('CommonLabels.ActionLink.Size.Default')
    .replace('{size}', defaultSizeLabel);

  return [
    { key: undefined, label: defaultSizeText },
    ...choices,
  ];
}

const ActionLinkDesignEditor = defineComponent({
  components: {
    ComboBoxEditorControl,
    ColorBoxEditorControl,
    CheckboxToggleEditorControl,
  },
  props: {
    value: {
      required: false,
      default: undefined,
      type: Object as PropType<ActionLinkSettings>,
    },
    defaults: {
      required: true,
      type: Object as PropType<ActionLinkDefaults>,
    },
    siteSettings: {
      required: false,
      default: undefined,
      type: Object as PropType<GlobalDesign>,
    },
    colorList: {
      required: true,
      type: Array as PropType<string[]>,
    },
    autoColorEnabled: {
      required: true,
      type: Boolean,
    },
    alphaColorEnabled: {
      required: true,
      type: Boolean,
    },
  },
  setup(props, { root, emit }) {
    const i18n = useI18N(root.$store);

    const settings = computed(() => ({
      appearance: props?.value?.appearance ?? props.defaults.appearance as ActionLinkAppearance,
      font: props.value?.font,
      size: props.value?.size,
      style: props.value?.style ?? props.defaults.style as ButtonStyle,
      color: props.value?.color,
      visible: props.value?.visible ?? props.defaults.visible,
    }));

    const appearance = computed({
      get: () => settings.value.appearance,
      set: useAppearanceChangeHandler(settings, emit),
    });
    const appearanceChoices: ComboBoxChoice[] = [
      {
        key: ActionLinkAppearance.SOLID_BUTTON,
        label: i18n('CommonLabels.ActionLink.Appearance.Solid'),
      },
      {
        key: ActionLinkAppearance.OUTLINE_BUTTON,
        label: i18n('CommonLabels.ActionLink.Appearance.Outline'),
      },
      {
        key: ActionLinkAppearance.TEXT_LINK,
        label: i18n('CommonLabels.ActionLink.Appearance.Link'),
      },
    ];

    const font = computed({
      get: () => getFontIfExists(settings.value.font),
      set: useFontChangeHandler(settings, emit),
    });
    const fontChoices = computed(() => {
      const defaultValue = calculateActualFontDefault(
        props.defaults.font,
        props.siteSettings,
      );
      const defaultFontText = i18n('CommonLabels.ActionLink.Font.Default')
        .replace('{fontName}', fontFamilyValue(fontWithFallback(defaultValue)) ?? '');

      return [
        { key: undefined, label: defaultFontText },
        ...fontKeyLabelList(),
      ];
    });

    const size = computed({
      get: () => settings.value.size,
      set: useSizeChangeHandler(settings, emit),
    });
    const sizeChoices = computed(() => calculateSizeChoices(props.defaults.size, i18n));

    const style = computed({
      get: () => settings.value.style,
      set: useStyleChangeHandler(settings, emit),
    });
    const styleChoices: ComboBoxChoice[] = [
      { key: ButtonStyle.PILL, label: i18n('CommonLabels.ActionLink.Style.Pill') },
      { key: ButtonStyle.RECTANGLE, label: i18n('CommonLabels.ActionLink.Style.Rectangle') },
      { key: ButtonStyle.ROUND_CORNER, label: i18n('CommonLabels.ActionLink.Style.Round') },
    ];

    const defaultColor = computed(() => calculateActualColorDefault(
      props.defaults.color,
      props.siteSettings,
    ));
    const color = computed({
      get: () => settings.value.color,
      set: useColorChangeHandler(settings, emit),
    });

    const visibility = computed({
      get: () => settings.value.visible,
      set: useVisibleChangeHandler(settings, emit),
    });

    return {
      i18n,
      appearance,
      appearanceChoices,
      font,
      fontChoices,
      size,
      sizeChoices,
      style,
      styleChoices,
      defaultColor,
      color,
      visibility,
    };
  },
});

export default ActionLinkDesignEditor;
