import { Color } from '@/app/shared/client/type/color';
import { Font } from '@/app/shared/client/type/font';
import { GlobalColorsString, GlobalFontsString } from '@/app/shared/client/type/global-string';

export enum ActionLinkAppearance {
  SOLID_BUTTON = 'solid-button',
  OUTLINE_BUTTON = 'outline-button',
  TEXT_LINK = 'text-link',
}

export enum ActionLinkSize {
  SMALL = 'small',
  MEDIUM = 'medium',
  LARGE = 'large',
}

export enum ButtonStyle {
  ROUND_CORNER = 'round-corner',
  RECTANGLE = 'rectangle',
  PILL = 'pill'
}

export interface ActionLinkSettings {
  appearance?: ActionLinkAppearance;
  font?: Font;
  size?: ActionLinkSize;
  style?: ButtonStyle;
  color?: Color;
  visible?: boolean;
}

export interface ActionLinkDefaults {
  appearance?: ActionLinkAppearance;
  font?: Font | GlobalFontsString;
  size?: ActionLinkSize;
  style?: ButtonStyle;
  color?: Color | GlobalColorsString;
  visible?: boolean;
}
