import {
  GlobalDesign,
} from '@/app/shared/client/type/tile/global';
import { Color } from '@/app/shared/client/type/color';
import {
  extractGlobalColor,
  extractGlobalFont,
  extractGlobalTextSize,
} from '@/app/editor/client/logic/site-settings';
import { Font } from '@/app/shared/client/type/font';
import {
  GlobalColorsString,
  GlobalFontsString,
  GlobalTextSizeString,
} from '@/app/shared/client/type/global-string';

export function calculateActualFontDefault(
  fontDefault: Font | GlobalFontsString | undefined,
  siteDesignSettings: GlobalDesign,
): Font | undefined {
  if (fontDefault?.startsWith('global.')) {
    return extractGlobalFont(
      fontDefault as GlobalFontsString,
      siteDesignSettings,
    );
  }

  return fontDefault as Font | undefined;
}

export function calculateActualTextSizeDefault(
  textSizeDefault: number | GlobalTextSizeString | undefined,
  siteDesignSettings: GlobalDesign,
): number | undefined {
  if (textSizeDefault?.toString()?.startsWith('global.')) {
    return extractGlobalTextSize(
      textSizeDefault as GlobalTextSizeString,
      siteDesignSettings,
    );
  }

  return textSizeDefault as number | undefined;
}

export function calculateActualColorDefault(
  colorDefault: Color | GlobalColorsString | undefined,
  siteDesignSettings?: GlobalDesign,
): Color | undefined {
  if (colorDefault?.toString()?.startsWith('global.')) {
    return extractGlobalColor(
      colorDefault as GlobalColorsString,
      siteDesignSettings,
    );
  }

  return colorDefault as Color;
}

export default {};
